import React, { Component }  from 'react';

import "../mycss/escolaridade.css"
import unicampLogo from "../myimg/unicampLogo.png"
import unespLogo from "../myimg/unespLogo.png"

export default class Escolaridade extends Component{

    

    render(){
        return(
        <div className="escolaridade" id="Formações-academicas">
            <div className="container">
                <div>
                    <div className="typewriter-div">
                        <div className={this.props.className}>
                            <h1 className="seccao-da-escolaridade">
                                {this.props.titulo}
                            </h1>
                        </div>
                    </div>

                    <div className="row row-info">

                        <div className="col-3 logo-div">
                            <img src={unicampLogo} className="escolaLogo"/>
                        </div>
                        <div className="col-8 info-div-unicamp">
                            <h2 className="titulo-info">
                                Cotil - Unicamp
                                <br/>
                                Colégio Técnico de Limeira - Universidade estadual de campinas
                            </h2>
                            <p className="info">
                                Estudei no COTIL no periodo de 2017 ~ 2019, lá cursei medio + tecnico em informatica.
                                <br/>
                                Durante esse periodo participei de iniciações cientificas, aulas extra curriculares, ministrei uma aula sobre Java Spring Boot
                                <br/>
                                Para concluir o meu tecnico, tive que realizar 600 horas de estagio e fazer um projeto de conclusão de curso (TCC)
                                <br/>
                                O COTIL é a escola de ensino medio e tecnico da UNICAMP, po isso possui a mesma qualidade da universidade
                            </p>
                        </div>
                    </div>

                    <div className="row">

                        <div className="col-8 info-div-unesp">
                            <h2 className="titulo-info">
                                Unesp - Bauru
                                <br/>
                                Universidade estadual de são paulo - Bauru / sp
                            </h2>
                            <p className="info">
                                Entrei na unesp em 2020 no curso de bacharelado em sistemas de informação, com previsão de concluir em 2025.
                                <br/>
                                A grade de materias incluem todos os aspectos do desenvolviemto e analize de sistemas da informação.
                                
                            </p>
                        </div>

                        <div className="col-4">
                            <img src={unespLogo} className="escolaLogo"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>)
    }

}